html{ 
    font-size: 1px;/*for using REM units*/
 }
 body{
   color: #222;
   font-size: 1rem;
   font-family: 'Source Serif Pro', serif;
 }

 :root{
   --text-font-size: 0.5rem; 
}

h1{
   font-size: 2.5rem;
   text-align: center;  
   letter-spacing: .2rem;
}

legend{
   font-size: 1.5rem;
}

p#answer{
   text-align: center;
}

p#copyRight{
   font-size: 0.8em;
   text-align: center;
}

@media screen and (min-width: 500px) {
   h1{
       font-size: 3rem;
   }
}

@media screen and (min-width: 600px) {
   h1{
       font-size: 4rem;
   }

}
@media screen and (min-width: 900px) {
      html{
        font-size: 20px;
      }
      h1{
        letter-spacing: 0.7rem;
      }
   }
#scorePanel{
      text-align: center;
}